import React from 'react'
import Base from "../core/Base"
import { useState } from "react"
import {isAutheticated} from "../auth/helper"
import {Link} from "react-router-dom";
import {createCategory} from "./helper/adminapicall"
const AddCategory = () =>{
    
    const [name, setName] = useState("")
    const [error, setError] = useState(false);
    const [success, setSuccess] = useState(false)

    const {user, token} = isAutheticated();

    const gobaack = () =>(
        <div className= "mt-5">
          <Link className= "btn btn-sm btn-info mb-3" to="/admin/dashboard">
              Admin home screen
          </Link>
        </div>
    )
    const handleChange = (event) =>{
        //
        setError("");
        setName(event.target.value)
    }
    const onSubmit = (event) =>{
        event.preventDefault();
        setError("");
        setSuccess(false);

        //backend request fired
        createCategory(user._id, token, {name})
        .then(data =>{
            if(data.error){
                setError(true)
            }
            else{
                setError("")
                setSuccess(true)
                setName("")
            }
        })

    }
    const succesMessage = () =>{
        if(success){
            return <h4 className="text-success">Category create</h4>
        }
    }
    const warningMessage = () =>{
        if(error){
            return <h4 className= "text-success">failed to create category</h4>
        }
    }
    const myCattegoryForm = () => (
        <form>
            <div className="form-group">
                <p className= "lead">Enter the category</p>

                <input type = "text"
                onChange= {handleChange}
                value= {name}
                className= "form-control my-3" 
                autoFocus required placeholder=" For EX. Summer"/>
                <button onClick= {onSubmit} className= "btn btn-outline-info"> Create new category</button>
            </div>
        </form>
    )
    return (
        <Base title= "Create a Category here" 
        description= "Add a new category for new tshirt" 
        className="container bg-info p-4">
            <div className="row bg-white rounded">
                <div className= "col-md-5 offset-md-2">
                    {succesMessage()}
                    {warningMessage()}
                    {myCattegoryForm()}
                     {gobaack()}
                </div>

            </div>
        </Base>
    )
}

export default AddCategory